import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'

const start = keyframes`
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
`;

const Start = styled.div`
  opacity: 0;
  animation: ${start} 3s 0.2s cubic-bezier(0.2, 0.8, 0.2, 1);
  animation-fill-mode: forwards;
`;

const StyledText = styled.span`
  font-family: 'Snell Roundhand', 'Snell Roundhand Script', serif;
  font-weight: bold;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);

  :hover {
    color: #ffe0e0
  }
`;

const Content = styled.div`
  height: 100%
  background-size: cover;
  background-position: center;
  cursor: url(../assets/cursor/empty.svg), auto;
  position: float;
`;

const ContentGroup = styled.div`
  margin: 0 auto;
  max-width: 1180px;
  font-size: 100px;
  padding: 50px 0px;
  
  @media (max-width: 1382px) {
    max-width: 80%
  } 

  @media (max-width: 1024px) {
    font-size: 80px;
  }

  @media (max-width: 768px) {
    max-width: 90%
    font-size: 60px;
    padding: 15px 0px;
  } 
`;

class IndexPage extends React.Component {
  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                HI, I'M <StyledText>Jaden</StyledText>. I'M A <StyledText>Computer Science</StyledText> MAJOR AND <StyledText>Art</StyledText> MINOR AT <StyledText>Pomona College</StyledText> WHO IS PASSIONATE ABOUT THE INTERSECTION OF <StyledText>Technology</StyledText> AND <StyledText>Design</StyledText>.
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default IndexPage



